body {
  margin: 0;
  padding: 0;
  font-family: "Montserrat", sans-serif;
  background: #2766b9;
}

h1,
h2 {
  text-transform: uppercase;
  letter-spacing: 0.1em;
}

.app {
  text-align: center;
}

.app-footer {
  height: 100px;
  background: #2766b9;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.app-footer a {
  padding: 0.5em;
  margin: 1em;
  color: #f6f5f5;
  text-decoration: none;
  font-size: 0.9em;
}

.app-footer a:hover {
  -webkit-transform: translateY(-2px);
      -ms-transform: translateY(-2px);
          transform: translateY(-2px);
  -webkit-transition: 0.2s ease;
  -o-transition: 0.2s ease;
  transition: 0.2s ease;
}

.not-found h1 {
  color: #f6f5f5;
  font-size: 5em;
  padding: 1.5em;
}

/* Styles for write-up components */
.container {
  max-width: 1000px;
  text-align: center;
}

.writeup-header {
  height: 75px;
  background: #2766b9;
}

.writeup-main {
  background: #f6f5f5;
}

.writeup-main h1,
.writeup-main h2 {
  color: #6e9fc6;
}

.writeup-main p {
  color: #2766b9;
}

.writeup-main a {
  color: #5e97d2;
}



